.App {
  display: flex;
  justify-content: center;
  background-color: #aeb1b8;
  min-height: 100vh;
  text-align: center;
}

.App canvas {
  max-width: 80vw;
  height: 300px;
}

.App-logo {
  animation: spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.loader {
  border: 16px solid #f3f3f37f;
  border-top: 16px solid #34a6db;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin: auto;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
